.main{
   
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}
.wrap{
    width: 100%;
}
#contact{
   background-color: #FECB16;
   width: 100%  ;
   border-radius: 20px;
   padding: 0px;
   box-shadow: 5px 5px 10px 1px rgba(0,0,0,0.2);
}
 
 #inputfield{
    padding-top: 10%;
    backdrop-filter: blur(10px);
     background-color: rgba(255, 255, 255, 0.5);
     margin-left: 15%;
     width:40%;
     border-top-left-radius: 140px;
      left: -10px;
      border-bottom-left-radius: 140px;
 }
 
 
 .input-field{
   display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    position: relative;
    padding: 0 10px 0 10px;
     
   
 }
 
 .input-field label{
   font-size: 20px;
   color: black;
   margin-left: 20px;
 }
  
 .input{
   height: 45px;
   width: 80%;
   background: transparent;
   border: none;
   border-bottom: 1px solid rgba(0, 0, 0, 0.2);
   outline: none;
   margin-bottom: 20px;
   margin-top: 40px;
   color: black;
   cursor: text;
   font-size: 20px;
   margin-left: 20px;
  
}
.input-box 
.input-field label{
   position: absolute;
   top: 10px;
   left: 10px;
   pointer-events: none;
   transition: .5s;
}
.input-field input:focus ~ label
   {
    top: -10px;
    font-size: 23px;
  }
  .input-field input:valid ~ label
  {
   top: -10px;
   font-size: 23px;
   color: #5d5076;
 }
 #image{
   width: 50%;
  margin-left: -1%;
 }
 
 /* button */

 #inputfield button{
 display: flex;
  width:   200px;
   height: 40px;
   font-size: 30px;
   align-items: center;
   justify-content: center;
 margin-left: 30%;
 margin-top: 30px;
 padding: 30px;
 background-color: transparent;
 border-radius: 20px;
 }
 #inputfield button:hover{

    background-color:#FECB16;
    transition: 0.6s ease;

 }